main.App-home {
    display: block;
    margin-bottom: 20px;
}
main.App-home .App-home__services .App-home__services-title {
    margin-bottom: 60px;
    text-align: center;
}
main.App-home .App-home__services .App-home__services-title p{
   font-size: 18px; 
}
main.App-home .App-home__services .App-home__services-title h2 {
    display: block;
    width: 100%;
    font-size: 36px;
    color: #2dace3;
    font-weight: 700;
    margin-bottom: 10px;
}


main.App-home .App-home__services .App-home__services-images {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
}

main.App-home .App-home__services .App-home__services-images .App-home__services-image {
    margin-bottom: 30px;
}

main.App-home .App-home__services .App-home__services-images .App-home__services-image img{
    display: block;
    width: 100%;
    height: auto;
    transition: .3s;
}

main.App-home .App-home__services .App-home__services-images .App-home__services-image img:hover {
    opacity: .8;
}

@media only screen and (max-width: 768px) {
    main.App-home .App-home__services .App-home__services-images {
        column-gap: 30px;
    }
    main.App-home .App-home__services .App-home__services-title {
        margin-bottom: 40px;
    }
}