main.App-quemSomos {
    display: block;
    padding-bottom: 30px;
}
main.App-quemSomos .App-quemSomos__section {
    margin-bottom: 30px;
}
main.App-quemSomos .App-quemSomos__banner img{
    display: block;
    width: 100%;
    height: auto;
}
main.App-quemSomos .App-quemSomos__banner {
    margin-bottom: 60px;

}
main.App-quemSomos .App-quemSomos__text {
    display: flex;
    font-size: 16px;
}
main.App-quemSomos .App-quemSomos__text strong {
    font-weight: 700;
}
main.App-quemSomos .App-quemSomos__text h1{
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 20px;
}
main.App-quemSomos .App-quemSomos__text h3{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
}
main.App-quemSomos .App-quemSomos__text p{
    margin-bottom: 15px;
    line-height: 20px;
}
main.App-quemSomos .App-quemSomos__text div {
    width: 60%;
}
main.App-quemSomos .App-quemSomos__text div + div {
    width: 40%;
}
main.App-quemSomos .App-quemSomos__text img {
    display: block;
    margin: 0 auto;
}

main.App-quemSomos .App-quemSomos__section-banner img {
    width: 100%;
    height: auto;
}

main.App-quemSomos .App-quemSomos__section-banner {
    position: relative;
}

main.App-quemSomos .App-quemSomos__section-banner .App-quemSomos__section-title{
    text-align: center;
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
}
main.App-quemSomos .App-quemSomos__section h2{
    color: #2dace3;
    font-weight: 700;
}
main.App-quemSomos .App-quemSomos__section-banner .App-quemSomos__section-title h2{
    font-size: 48px;
    margin-bottom: 50px;
}

main.App-quemSomos .App-quemSomos__section-banner .App-quemSomos__section-title p {
    font-size: 18px;
    line-height: 20px;
}

main.App-quemSomos .App-quemSomos__section > h2{
    font-size: 36px;
    text-align: center;
    margin-bottom: 40px;
}

main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 30px;
    text-align: center;
}
main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits h4 {
    color: #2dace3;
    font-weight: 700;
    font-size: 18px;
    max-width: 250px;
    margin: 0 auto 10px;
}
main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits li{
    margin-bottom: 30px;
}
main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits img {
    width: auto;
    height: 80px;
    margin-bottom: 30px;
}

@media only screen and (max-width: 768px) {
    main.App-quemSomos {
        padding-bottom: 0;
    }
    main.App-quemSomos .App-quemSomos__section {
        margin-bottom: 20px;
    }
    main.App-quemSomos .App-quemSomos__banner {
        margin-bottom: 40px;
    
    }
    main.App-quemSomos .App-quemSomos__text {
        text-align: center;
    }
    main.App-quemSomos .App-quemSomos__text div {
        width: 100%;
    }
    main.App-quemSomos .App-quemSomos__text div + div {
        display: none;
    }
    main.App-quemSomos .App-quemSomos__text img {
        display: block;
        margin: 0 auto;
    }
    
    main.App-quemSomos .App-quemSomos__section-banner .App-quemSomos__section-title{
        text-align: center;
        position: absolute;
        bottom: 15px;
        left: 0%;
        transform: none;
    }
    main.App-quemSomos .App-quemSomos__section-banner .App-quemSomos__section-title h2{
        font-size: 24px;
        margin-bottom: 10px;
    }
    
    main.App-quemSomos .App-quemSomos__section-banner .App-quemSomos__section-title p {
        font-size: 12px;
        line-height: 18px;
        font-weight: 700;
    }
    
    main.App-quemSomos .App-quemSomos__section > h2{
        font-size: 24px;
    }
    
    main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits {
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }
    main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits h4 {
        color: #2dace3;
        font-weight: 700;
        font-size: 16px;
    }
    main.App-quemSomos .App-quemSomos__section .App-quemSomos__benefits img {
        width: auto;
        height: 60px;
        margin-bottom: 20px;
    }    
}
