main.App-faleconosco {
    display: block;
    padding-bottom: 30px;
}
main.App-faleconosco .App-faleconosco__section {
    margin-bottom: 30px;
}

main.App-faleconosco .App-faleconosco__banner img{
    display: block;
    width: 100%;
    height: auto;
}
main.App-faleconosco .App-faleconosco__banner {
    margin-bottom: 60px;
}
main.App-faleconosco .App-faleconosco__text p{
    display: block;
    font-size: 16px;
    margin-bottom: 10px;
}
main.App-faleconosco .App-faleconosco__text div{
    margin-bottom: 30px;
}
main.App-faleconosco .App-faleconosco__text h2{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 20px;
    color:#2dace3;
}
main.App-faleconosco .App-faleconosco__text h3{
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 20px;
    color:#000;
}
main.App-faleconosco .App-faleconosco__section.App-faleconosco__section--form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
}
main.App-faleconosco .App-faleconosco__form input, main.App-faleconosco .App-faleconosco__form textarea{
    display: block;
    width: calc(50% - 15px);
    border: 1px solid #2dace3;
    border-radius: 6px;
    padding: 10px 15px;
    font-size: 14px;
    
}
main.App-faleconosco .App-faleconosco__form .half {
    display: flex;
    width: 100%;
    justify-content: space-between;
}
main.App-faleconosco .App-faleconosco__form .half > div {
    width: calc(50% - 15px);
}
main.App-faleconosco .App-faleconosco__form .half input, main.App-faleconosco .App-faleconosco__form .half textarea{
    width: 100%;
}
main.App-faleconosco .App-faleconosco__form textarea{
    resize: none;
    width: 100% !important;
    height: 120px !important;
}
main.App-faleconosco .App-faleconosco__form button{
    display: block;
    resize: none;
    width: 100%;
    max-width: 200px;
    height: 40px;
    background: #2dace3;
    border: none;
    color: #fff;
    font-weight: 700;
    font-size: 18px;
    margin: 0 auto;
    cursor: pointer;
    transition: .3s;
}
main.App-faleconosco .App-faleconosco__form #msg{
    display: block;
    margin: 20px auto;
    text-align: center;
    font-size: 16px;
    font-weight: 700;
}
main.App-faleconosco .App-faleconosco__form button:hover{
    opacity: .7;
}
main.App-faleconosco .App-faleconosco__form label{
    color: #2dace3;
    font-weight: 700;
    display: block;
    margin-bottom: 5px; 
}
main.App-faleconosco .App-faleconosco__form div {
    margin-bottom: 15px;
}
main.App-faleconosco .App-faleconosco__section iframe{
    width: 100%;
}
@media only screen and (max-width: 768px) {
    main.App-faleconosco {
        padding-bottom: 0;
    }
    main.App-faleconosco .App-faleconosco__section {
        margin-bottom: 20px;
    }
    main.App-faleconosco .App-faleconosco__banner {
        margin-bottom: 40px;
    }
    main.App-faleconosco .App-faleconosco__text {
        margin-bottom: 50px;
    }
    main.App-faleconosco .App-faleconosco__text p{
        margin-bottom: 0;
    }
    main.App-faleconosco .App-faleconosco__text div{
        margin-bottom: 20px;
    }
    main.App-faleconosco .App-faleconosco__text h2{
        margin-bottom: 15px;
    }
    main.App-faleconosco .App-faleconosco__section.App-faleconosco__section--form {
        display: block;
        margin-bottom: 50px;
    }
    main.App-faleconosco .App-faleconosco__form input, main.App-faleconosco .App-faleconosco__form textarea{
        width: 100%;
    }

    main.App-faleconosco .App-faleconosco__form .half > div {
        width: calc(50% - 5px);
    }
    main.App-faleconosco .App-faleconosco__form button:hover{
        opacity: 1;
    }
    
}
