main.App-clientes {
    display: block;
    padding-bottom: 30px;
}
main.App-clientes .App-clientes__section {
    margin-bottom: 30px;
}
main.App-clientes .App-clientes__banner img{
    display: block;
    width: 100%;
    height: auto;
}
main.App-clientes .App-clientes__banner {
    margin-bottom: 60px;
}
main.App-clientes .App-clientes__text {
    display: block;
    font-size: 16px;
}
main.App-clientes .App-clientes__text strong {
    font-weight: 700;
}
main.App-clientes .App-clientes__text h1{
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 20px;
}

main.App-clientes .App-clientes__referencias {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 30px;
}
main.App-clientes .App-clientes__referencias  img{
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 768px) {
    main.App-clientes {
        padding-bottom: 0;
    }
    main.App-clientes .App-clientes__banner {
        margin-bottom: 40px;
    }
    
    main.App-clientes .App-clientes__referencias {
        grid-template-columns: 1fr 1fr;
        column-gap: 30px;
    }
    main.App-clientes .App-clientes__referencias li {
        margin-bottom: 30px;
    }
}
