body {
  display: block;
  padding-top: 125px;
  font-family: -apple-system, BlinkMacSystemFont, 'Arial', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
 width: 100%;
 max-width: 1280px;
 margin: 0 auto; 
 padding: 0 15px;
}

@media only screen and (max-width: 768px) {
  body {
    padding-top: 100px;
  }
}