main.App-servicos {
    display: block;
    padding-bottom: 30px;
}
main.App-servicos .App-servicos__section {
    margin-bottom: 30px;
}
main.App-servicos .App-servicos__banner img{
    display: block;
    width: 100%;
    height: auto;
}
main.App-servicos .App-servicos__banner {
    margin-bottom: 60px;
}
main.App-servicos .App-servicos__text p{
    display: block;
    font-size: 16px;
    margin-bottom: 10px;
}
main.App-servicos .App-servicos__text div{
    margin-bottom: 30px;
}
main.App-servicos .App-servicos__text h2{
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 20px;
}


@media only screen and (max-width: 768px) {
    main.App-servicos {
        padding-bottom: 0;
    }
    main.App-servicos .App-servicos__banner {
        margin-bottom: 40px;
    }
    
}
