.App-Xlider {
    margin-bottom: 60px;
}
.slick-slider .slick-arrow {
    background: #e3e3e3;
    border: none;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    font-size: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 5;
    cursor: pointer;
    transition: .3s;
}
.slick-slider .slick-arrow:hover {
    opacity: .6;
}
.slick-slider .slick-arrow::after {
    content: '';
    display: block;
    width: 20px;
    height: 20px;
    background-image: url('./icon-right.png');
}
.slick-slider .slick-arrow.slick-next::after {
    transform: rotate(180deg);
}
.slick-slider .slick-next {
    right: 15px;
}
.slick-slider .slick-prev {
    left: 15px;
}

.slick-slider img {
    width: 100%;
    height: auto;
}

@media only screen and (max-width: 768px) {
    .App-Xlider {
        margin-bottom: 40px;
    }
}