header.App-header {
    background: #2dace3;
    z-index: 10;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
}
header.App-header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;

}
header.App-header .App-logo{
    display: block;
    width: 100%;
    max-width: 140px;
    height: auto;
    transform: translateY(40%);
    transition: .3s;
    -webkit-box-shadow: 5px 5px 6px 0px rgba(0,0,0,0.2); 
    box-shadow: 5px 5px 6px 0px rgba(0,0,0,0.2);
    border-radius: 100%;
}
header.App-header .App-logo:hover {
    transform: translateY(35%); 
}
header.App-header .App-nav {
    width: 100%;
}
header.App-header .App-nav ul{
    display: flex;
    justify-content: space-around;
    transform: translateY(50%);
}

header.App-header .App-nav ul li a {
    font-size: 16px;
    color: #ffffff;
    text-transform: uppercase;
    font-weight: 700;
    transition: .3s;
}
header.App-header .App-nav ul li a:hover {
    opacity: .8;
    color: #000;
}
header.App-header .btn-menu, header.App-header .App-nav .App-nav-mb {
    display: none;
}

@media only screen and (max-width: 768px) {
    header.App-header .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    
    }
    header.App-header .App-logo{
        max-width: 100px;
        transform: translateY(40%);
    }
    header.App-header .App-nav {
        display: block;
        position: fixed;
        left: -100%;
        height: 100vh;
        top: 0;
        width: 80%;
        max-width: 400px;
        background: #fff;
        transition: .3s;
        -webkit-box-shadow: 5px 0px 6px 0px rgba(0,0,0,0.7); 
        box-shadow: 5px 0px 6px 0px rgba(0,0,0,0.7);
    }
    header.App-header .App-nav.open {
        left: 0;
    }
    header.App-header .App-nav ul{
        display: block;
        transform: none;
        text-align: center;
    }
    header.App-header .App-nav ul li {
        margin-bottom: 20px;
        padding: 0 15px;
    }
    header.App-header .App-nav ul li a {
        font-size: 16px;
        color: #000;
    }
    header.App-header .App-nav ul li a:hover {
        opacity: 1;
    }   
    header.App-header .btn-menu {
        display: block;
        border: none;
        width: 45px;
        cursor: pointer;
        background: transparent;
    } 
    header.App-header .btn-menu img {
        width: 100%;
        height: auto;
    }
    header.App-header .App-nav .App-nav-mb {
        display: block;
    }
    header.App-header .App-nav .App-nav-title {
        position: relative;
        width: 100%;
        background: #2dace3;
        padding: 20px 15px;
        text-align: center;
    }
    header.App-header .App-nav .App-nav-title h3 {
        font-size: 24px;
        text-transform: uppercase;
        font-weight: 700;
        color: #fff;
    } 
    header.App-header .App-nav .App-nav-title .btn-close-menu{
        position: absolute;
        left: 15px;
        top: 20px;
        height: 25px;
        width: 25px;
        background: transparent;
        border: none;
        cursor: pointer;
        padding: 0;
    }   
    header.App-header .App-nav .App-nav-title .btn-close-menu img {
        width: 100%;
        height: auto;
    }
}