footer.App-footer {
    display: block;
    background: #2dace3;
    width: 100%;
    padding: 20px 0;
    color: #fff;
}
footer.App-footer nav {
    display: flex;
    justify-content: space-between;
}
footer.App-footer nav ul li:not(.horarios) {
    display: flex;
    align-items: center;
}
footer.App-footer a{
    font-size: 16px;
    color: #fff;
    transition: .3s;
}
footer.App-footer ul li {
    margin-bottom: 15px;
}
footer.App-footer ul li:last-child {
    margin-bottom: 0;
}
footer.App-footer a:hover {
    opacity: .8;
    color: #000;
}

footer.App-footer .endereco,  footer.App-footer .horarios{
    text-align: center;
}
footer.App-footer .endereco p{
    line-height: 24px;
}
footer.App-footer .phones span {
    margin: 0 5px;
}

footer.App-footer .App-icon {
    width: 30px;
    height: auto;
    margin-right: 10px;
}


@media only screen and (max-width: 768px) {
    footer.App-footer {
        padding: 30px 0;
    }
    footer.App-footer nav {
        display: block;
    }
    footer.App-footer nav ul li:not(.horarios) {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    footer.App-footer ul {
        margin-bottom: 20px;
    }
    footer.App-footer ul:last-child {
        margin-bottom: 0;
    }
    footer.App-footer ul li {
        margin-bottom: 20px;
    }
    footer.App-footer a:hover {
        opacity: 1;
        color: #fff;
    }
    
    footer.App-footer .App-icon {
        width: 25px;
    }
}